import React, { useEffect, useState } from 'react';
import axios from 'axios';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import { useNavigate, useParams } from 'react-router-dom';

const EditSiswa = () => {
  const [nama, setNama] = useState('');
  const [jurusan, setJurusan] = useState('');
  const [umur, setUmur] = useState('');
  const navigate = useNavigate();
  const { id } = useParams();

  useEffect(() => {
    getSiswaById();
  }, []);

  const updateSiswa = async (e) => {
    e.preventDefault();
    try {
      await axios.patch('http://localhost:5000/siswa/' + id, {
        nama,
        jurusan,
        umur
      });
      navigate('/');
    } catch (error) {
      console.log(error);
    }
  };

  const getSiswaById = async () => {
    const response = await axios.get(`http://localhost:5000/siswa/${id}`);
    setNama(response.data.nama);
    setJurusan(response.data.jurusan);
    setUmur(response.data.umur);
  };

  return (
    <div>
      <h1 className="my-3">Edit Siswa</h1>
      <Form onSubmit={updateSiswa}>
        <Form.Group className="mb-3">
          <Form.Label>Nama</Form.Label>
          <Form.Control
            type="text"
            placeholder="Masukkan Nama"
            value={nama}
            onChange={(e) => setNama(e.target.value)}
            required
          />
        </Form.Group>
        <Form.Group className="mb-3">
          <Form.Label>Jurusan</Form.Label>
          <Form.Control
            type="text"
            placeholder="Masukkan Jurusan"
            value={jurusan}
            onChange={(e) => setJurusan(e.target.value)}
            required
          />
        </Form.Group>
        <Form.Group className="mb-3">
          <Form.Label>Umur</Form.Label>
          <Form.Control
            type="number"
            placeholder="Masukkan Umur"
            value={umur}
            onChange={(e) => setUmur(e.target.value)}
            required
          />
        </Form.Group>
        <Button variant="success" type="submit">
          Update
        </Button>
      </Form>
    </div>
  );
};

export default EditSiswa;
