import React, { useEffect, useState } from 'react';
import axios from 'axios';
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';
import { Link } from 'react-router-dom';

const SiswaList = () => {
  const [datas, setDatas] = useState([]);

  useEffect(() => {
    getSiswaList();
  }, []);

  const getSiswaList = async () => {
    const response = await axios.get('http://localhost:5000/siswa');
    setDatas(response.data);
  };

  const deleteSiswa = async (id) => {
    try {
      await axios.delete(`http://localhost:5000/siswa/${id}`);
      getSiswaList();
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <div>
      <h1 className="my-3">List Siswa</h1>
      <Table striped bordered hover>
        <thead>
          <tr>
            <th>No</th>
            <th>Nama</th>
            <th>Jurusan</th>
            <th>Umur</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          {datas.map((data, i) => (
            <tr key={i + 1}>
              <td>{i + 1}</td>
              <td>{data.nama}</td>
              <td>{data.jurusan}</td>
              <td>{data.umur}</td>
              <td className="w-25">
                <Link to={'/edit/' + data._id}>
                  <Button variant="primary" className="m-2">
                    Edit
                  </Button>
                </Link>
                <Button variant="danger" onClick={() => deleteSiswa(data._id)}>
                  Delete
                </Button>
              </td>
            </tr>
          ))}
        </tbody>
      </Table>
      <Link to={'/add'}>
        <Button variant="success" className="mt-2">
          Tambah Siswa
        </Button>
      </Link>
    </div>
  );
};

export default SiswaList;
